<template>
  <div class="vue-number-counter-wrapper">
    <div class="split-number" :style="{transition: `transform ${option.duration}ms`}"
         v-for="(item, index) in splitCharacterArr" :key="index">
      <div class="number-unit"
           :class="{'active-number': item===subItem}"
           :style="{width: `${checkWidth()}px`}"
           v-for="(subItem, subIndex) in characterMap" :key="subIndex">
        {{ subItem }}
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.vue-number-counter-wrapper {
  position: relative; // absolutewidth
  overflow: hidden;

  .split-number {
    display: inline-block;
    transition: all 2s;

    .number-unit {
      // width: 24px;
      // height: 20px;
    }
  }
}
</style>
<script>
export default {
  name: 'vNumfy',
  props: {
    'value': { // 
      type: [String, Number],
      // required: true
      default: '0'
    },
    'option': {
      type: Object,
      default: () => {
        // console.log('props');
        return {
          duration: 2000, // 
          characterWidth: 16, // 
          characterSmallWidth: 8,
          additionalSmallChars: [],
          addCharacter: [], // ['', '']
          replaceCharacterMap: [], // 
          decimals: 2, // :valueNumber
          thousandsSeparatorFlag: false // :valueNumber
        }
      }
    }
  },
  data() {
    return {
      // 
      characterMap: [',', '+', '-', '.', '9', '8', '7', '6', '5', '4', '3', '2', '1', '0'],
      smallCharacters: [',', '+', '-', '.']
    }
  },
  computed: {
    // 
    splitCharacterArr() {
      let val = this.value;
      // :valueNumber
      if (typeof val === 'number') {
        if (this.option.decimals >= 0) {
          val = val.toFixed(this.option.decimals); // 
        }
        if (this.option.thousandsSeparatorFlag) {
          val = this.addThousandsSeparator(val); // 
        }
      }
      return String(val).split('');
    }
  },
  methods: {
    checkWidth() {
      return this.option.characterWidth
   /*   if (this.smallCharacters.indexOf(char) > -1) {
        return this.option.characterSmallWidth
      } else {
        return this.option.characterWidth
      }*/
    },
    // 
    play(characterArr) {
      // console.log(characterArr);
      let splitNumberDoms = this.$el.getElementsByClassName('split-number');
      let numberUnitDoms = this.$el.getElementsByClassName('number-unit');
      // indexindex
      characterArr.forEach((item, index) => {
        let indexInCharacterMap = -1;
        this.characterMap.forEach((x, i) => {
          if (x === item) {
            indexInCharacterMap = i;
          }
        });
        if (indexInCharacterMap === -1) { // 
          // eslint-disable-next-line no-console
          console.warn(`There's no Character[${item}], please use :option.addCharacter to add!!`);
        } else {
          let offset = -indexInCharacterMap * numberUnitDoms[0].offsetHeight;
          splitNumberDoms[index].style.transform = `translateY(${offset}px)`;
        }
      });
    },
    // option
    optionValidateAndSetDefaultValue() {
      if (!this.option.hasOwnProperty('duration') || this.option.duration < 0) {
        this.option.duration = 2000;
      }
      if (!this.option.hasOwnProperty('characterWidth') || this.option.characterWidth < 0) {
        this.option.characterWidth = 16;
      }
      if (!this.option.hasOwnProperty('addCharacter')) {
        this.option.addCharacter = [];
      }
      if (!this.option.hasOwnProperty('replaceCharacterMap')) {
        this.option.replaceCharacterMap = [];
      }
      if (!this.option.hasOwnProperty('decimals')) {
        this.option.decimals = 2;
      }
      if (!this.option.hasOwnProperty('thousandsSeparatorFlag')) {
        this.option.thousandsSeparatorFlag = false;
      }
      // Set
      if (this.option.addCharacter.length) this.characterMap = Array.from(new Set([...this.option.addCharacter, ...this.characterMap]));
      // Set
      if (this.option.replaceCharacterMap.length) this.characterMap = Array.from(new Set(this.option.replaceCharacterMap));
    },
    // 
    addThousandsSeparator(strNum) {
      if (strNum.length <= 3) {
        return strNum;
      }
      if (!/^(\+|-)?(\d+)(\.\d+)?$/.test(strNum)) {
        return strNum;
      }
      let a = RegExp.$1;
      let b = RegExp.$2;
      let c = RegExp.$3;
      let re = new RegExp();
      re.compile('(\\d)(\\d{3})(,|$)');
      while (re.test(b)) {
        b = b.replace(re, '$1,$2$3');
      }
      return a + b + c;
    }
  },
  watch: {
    // beforeCreate -> props -> watch -> computed -> created
    // optionwatchcomputedoption
    option: {
      // eslint-disable-next-line no-unused-vars
      handler(newVal, oldVal) {
        // console.log('watch');
        this.optionValidateAndSetDefaultValue(); // option
      },
      deep: true,
      immediate: true // truecomputedoption
    },
    // eslint-disable-next-line no-unused-vars
    splitCharacterArr(newVal, oldVal) {
      this.$nextTick(() => {
        this.play(newVal);
        // let numberUnitDoms = document.getElementsByClassName('number-unit');
        // for (let x of numberUnitDoms) {
        //   x.style.left = `${Math.random() * 200 - 100}px`;
        //   x.style.top = `${Math.random() * 200 - 200}px`;
        //   // x.style.transform = `rotate(${Math.random() * 360}deg)`;
        // }
      });
    }
  },
  beforeCreate() {
    // console.log('beforeCreate');
  },
  created() {
    // console.log('created');
  },
  mounted() {
    this.$nextTick(() => {
      this.play(this.splitCharacterArr);
    });
  },
  components: {},
  beforeDestroy() {
  }
}
</script>
